import CryptoJS from "crypto-js";
import { Buffer } from "buffer";

export interface FileDesc {
    name: string,
    remote_path: string,
    key: string,
    iv: string
};
export interface FolderDesc {
    name: string,
    remote_path: string
};
export interface Manifest {
    remote_path: string,
    default_iv: string,
    files: FileDesc[],
    folders: FolderDesc[]
};

export const repo_base = "https://huggingface.co/datasets/personpi/feimage/resolve/main/";
export const root_path = "dataset.json";
export const manifest_name = "manifest.json";
export const load_delay = 800;

export const empty_manifest: Manifest = {remote_path: root_path, default_iv: "", files: [], folders: []};

const decrypt_aes = (b64key: string, b64iv: string, b64ct: string) => {
    const key = CryptoJS.enc.Base64.parse(b64key);
    const iv = CryptoJS.enc.Base64.parse(b64iv);
    return CryptoJS.AES.decrypt(b64ct, key, { iv: iv, mode: CryptoJS.mode.CBC, padding: CryptoJS.pad.Pkcs7 });
};
const convertBlobToBase64 = (blob: Blob) => new Promise<string>((resolve, reject) => {
    const reader = new FileReader();
    reader.onerror = reject;
    reader.onload = () => {
        resolve(reader.result as string);
    };
    reader.readAsDataURL(blob);
});
const dec_key_iv = (enc_key: string, enc_iv: string, passwd: string, def_iv: string) => {
    const sha256_pw = CryptoJS.SHA256(passwd).toString(CryptoJS.enc.Base64);
    const default_iv = Buffer.from(def_iv, "utf-8").toString("base64");
    const key = decrypt_aes(sha256_pw, default_iv, enc_key).toString(CryptoJS.enc.Base64);
    const iv = decrypt_aes(sha256_pw, default_iv, enc_iv).toString(CryptoJS.enc.Base64);
    return [key, iv];
};

export const loadManifest = async (manifest_path: string) => {
    const manifest_url = repo_base + manifest_path;
    const resp = await fetch(manifest_url);
    const manifest = await resp.json();
    return manifest as Manifest;
};
export const loadImg = async (img_path: string, enc_key: string, enc_iv: string, passwd: string, def_iv: string) => {
    const [key, iv] = dec_key_iv(enc_key, enc_iv, passwd, def_iv);
    const img_url = repo_base + img_path;
    const resp = await fetch(img_url);
    const resp_blob = await resp.blob();
    const orig_b64 = await convertBlobToBase64(resp_blob);
    const b64 = orig_b64.split(',')[1];
    const dec_img_b64 = decrypt_aes(key, iv, b64).toString(CryptoJS.enc.Base64);
    const dec_img_binary = Buffer.from(dec_img_b64, 'base64');
    const img_blob = new Blob([dec_img_binary], {type: "image/jpeg"});
    return URL.createObjectURL(img_blob);
};
