import React, { useState } from 'react';
import { EyeInvisibleOutlined, EyeTwoTone } from '@ant-design/icons';
import { Button, Input, Space } from 'antd';

export interface PasswordProps {
    setPassword: React.Dispatch<React.SetStateAction<string>>
};

const Password: React.FC<PasswordProps> = (props) => {
    const [inputPassword, setInputPassword] = useState("");

    return <Space direction="horizontal" className='small-margin'>
        <Input.Password
        placeholder="Input repository password..."
        iconRender={(visible) => (visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />)}
        onChange={(e) => {setInputPassword(e.target.value)}}
        onPressEnter={(e) => {props.setPassword(e.currentTarget.value)}}
        />
        <Button onClick={() => {props.setPassword(inputPassword)}}>Submit</Button>
    </Space>;
};

export default Password;