import React, { useEffect, useState } from 'react';
import { List, Typography, Image } from 'antd';
import { FolderDesc, loadManifest, loadImg, Manifest, root_path, manifest_name, empty_manifest, load_delay } from './Api';

export interface FilelistProps {
    password: string
}

let upToDateImgUrl: string[] = [];

const Filelist: React.FC<FilelistProps> = (props) => {
    const [currentPath, setCurrentPath] = useState(root_path);
    const [manifest, setManifest] = useState<Manifest>(empty_manifest);
    const [loading, setLoading] = useState(false);
    const [imgUrls, setImgUrls] = useState<string []>([]);

    const updateImgUrl = (idx: number, newUrl: string) => {
        upToDateImgUrl = upToDateImgUrl.map((url, cur_idx, _) => (cur_idx === idx) ? (newUrl) : (url));
        return upToDateImgUrl;
    };

    useEffect(() => {
        setLoading(true);
        loadManifest(currentPath)
            .then(res => {
                setManifest(res);
                setLoading(false);
                upToDateImgUrl = Array.from(new Array(res.files.length), () => "");
                setImgUrls(upToDateImgUrl);
                res.files.forEach((img, idx, _) => {
                    setTimeout(() => {
                        loadImg(img.remote_path, img.key, img.iv, props.password, res.default_iv)
                            .then(imgUrl => {
                                setImgUrls(updateImgUrl(idx, imgUrl));
                            });
                    }, load_delay * idx);
                });
            })
            .catch(_=>{});
    }, [currentPath, props.password]);

    const parent_folder: FolderDesc = {
        name: "..",
        remote_path: (currentPath.split('/').length - 1 > 1) ?
            (currentPath + `/../../${manifest_name}`) : (root_path)
    };
    const folder_list = [parent_folder, ...manifest.folders];
    
    return <>
    <div className='small-margin'>
        <Typography.Text>Current path: </Typography.Text>
        <Typography.Text code>{currentPath}</Typography.Text>
    </div>
    <List
        bordered
        loading={loading}
        dataSource={folder_list}
        renderItem={(folder) => (
        <List.Item onClick={() => {setCurrentPath(folder.remote_path)}}>
            <Typography.Text>
                {folder.name}
            </Typography.Text>
        </List.Item>
        )}
        className="small-margin"
    />
    <Image.PreviewGroup>
        <List 
            grid={{column: 4}}
            dataSource={imgUrls}
            renderItem={(url) =>
                <Image src={url} className="image-grid" />
            }
        />
    </Image.PreviewGroup>
    </>;
};

export default Filelist;